import * as types from '../mutation-types'

const state = {
  sidebar: {
    opened: false,
    withoutAnimation: false
  },
  config: {
    googleMaps: {
      apiKey: 'AIzaSyBNAqPrTQoz9P4NBlDDyfxrnKiafkaL8iQ'
    },
    windowMatchSizeLg: '(min-width: 992px)',
    palette: {
      primary: '#4ae387',
      danger: '#e34a4a',
      info: '#4ab2e3',
      success: '#db76df',
      warning: '#f7cc36',
      white: '#fff',
      black: '#000',
      fontColor: '#34495e',
      transparent: 'transparent',
      lighterGray: '#ddd'
    }
  },
  isLoading: true,
  tableData: {data: [{
    // id: 2301000,
    // nome: 'Aquiraz',
    // microrregiao: {
    //   id: 23016,
    //   nome: 'Fortaleza',
    //   mesorregiao: {
    //     id: 2303,
    //     nome: 'Metropolitana de Fortaleza',
    //     UF: {
    //       id: 23,
    //       sigla: 'CE',
    //       nome: 'Ceará',
    //       regiao: {
    //         id: 2,
    //         sigla: 'NE',
    //         nome: 'Nordeste'
    //       }
    //     }
    //   }
    // }
  }]},
  dengueData: {
    // ano: '2017',
    // estado: 'Ceara',
    // id: '23',
    // sigla: 'CE'
  }
}

const mutations = {
  [types.CLOSE_MENU] (state) {
    if (document.documentElement.clientWidth < 992) {
      state.sidebar.opened = false
    }
  },
  [types.TOGGLE_SIDEBAR] (state, opened) {
    state.sidebar.opened = opened
  },
  [types.TOGGLE_WITHOUT_ANIMATION] (state, value) {
    state.sidebar.withoutAnimation = value
  },
  setLoading (state, isLoading) {
    state.isLoading = isLoading
  },
  setTableData (state, tableData) {
    state.tableData = tableData
  },
  setDengueData (state, dengueData) {
    state.dengueData = dengueData
  },
  updateTableDataItem (state, params) {
    state.tableData.data[params.itemIndex][params.field] = params.value
  }
}

const actions = {
  closeMenu ({ commit }) {
    commit(types.CLOSE_MENU)
  },
  toggleSidebar ({ commit }, opened) {
    commit(types.TOGGLE_SIDEBAR, opened)
  },
  isToggleWithoutAnimation ({ commit }, value) {
    commit(types.TOGGLE_WITHOUT_ANIMATION, value)
  }
}

export default {
  state,
  mutations,
  actions
}



